import * as m from "mithril";
import { Component, Renderer } from "meiosis";
import { meiosisRender, MeiosisRender, RenderIntoElement } from "meiosis-render";

function intoElement<M, P>(element: Element): Renderer<M, Mithril.VirtualElement, P> {
  return function(model: M, rootComponent: Component<M, Mithril.VirtualElement>): any {
    return m.render(element, rootComponent(model));
  };
}

function renderer<M, P>(): MeiosisRender<M, Mithril.VirtualElement, P> {
  return meiosisRender<M, Mithril.VirtualElement, P>(intoElement);
}

export { renderer };



